.modal {
     z-index: 1200;
    
    .flexible-modal {
        position: absolute;
        z-index: 1200;
        background: white;
        padding: 15px !important;
        overflow-y: auto;
    }
    .flexible-modal-mask {
        z-index: 1200;
        position: fixed;
        height: 100%;
        background: rgba(55, 55, 55, 0.3) !important;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

    }
    .flexible-modal-resizer {
        z-index: 1200;
        position: absolute;
        right: 0;
        bottom: 0;
        cursor: se-resize;
        margin: 10px;
        border-bottom: solid 3px #333;
        border-right: solid 3px #333;

    }
    .flexible-modal-drag-area {
        z-index: 1200;
        background: rgba(160, 213, 247, 0.3) !important;
        height: 50px;
        position: absolute;
        cursor: move;

    }
}
