.totalCalculator {
    padding: 0;
    margin: 10px;
    text-align: center;
    .calculator {
        width: 750px;
        background-color: rgba(160, 213, 247, 0.3);
        padding: 10px;
    }
    #display {
        width: 100%;
        background-color: #f8f8f8;
        padding: 10px 10px 20px 10px;
        margin-bottom: 10px;
        font-size: 18px;
        word-break: break-all;
        text-align: right;
        border: 1px solid #c0c0c0;
        border-radius: 4px;
      
    }
    .btnTop {
        color: white;
        background-color: #2196f3;
        font-size: 16px;
        border: none;
        margin: auto;
        width: 50px;
        height: 45px;
        font-weight: bold;
        border-radius: 4px;
        cursor: pointer;
    }
    .btnNum {
        color: white;
        background-color: #2196f3;
        font-size: 16px;
        border: none;
        margin: auto;
        width: 50px;
        height: 45px;
        font-weight: bold;
        border-radius: 4px;
        cursor: pointer;
    }
    .btnMath {
        color: white;
        background-color: #2196f3;
        font-size: 16px;
        border: none;
        margin: auto;
        width: 50px;
        height: 45px;
        font-weight: bold;
        border-radius: 4px;
        cursor: pointer;
    }
    .btnOpps {
        color: white;
        background-color: #2196f3;
        font-size: 16px;
        border: none;
        margin: auto;
        width: 50px;
        height: 45px;
        font-weight: bold;
        border-radius: 4px;
        cursor: pointer;
    }
    .buttons {
        height: 360px !important;
    }
    .theadOfButtons {
        
       min-height: 150px !important;
    }
}
