.notification {
    top: 185px;
    left: auto;
    right: 0;
    bottom: 0;
    position: fixed;
    margin: 0 auto;
    max-width: 500px;
    // width: 90%;
    height: max-content;
    // background: lighten($color-info, 30%);
    color: #000;
    word-break: break-word;
    z-index: 9999;

    &__close {
        font-size: 24px;
        line-height: 14px;
        color: #fff;
        position: absolute;
        cursor: pointer;
        right: 10px;
        top: 5px; // 8px
        display: none;

        &.error {
            color: #fff;
        }
        &.success {
            color: #fff;
        }
        &.info {
            color: #000;
        }
    }

    &__item {
        margin-top: 0px;
        padding: 17px 27px;
        position: relative;
        transition: 0.5s ease-in-out;
        align-items: center;
        justify-content: center;
        margin-top: 2px;
        margin-bottom: 5px;
        left: 100%;
        transition-property: left;
        transition-duration: 0.3s;
    }

    .error {
        background: #e9bbbb;
        color: #000;
    }
    .success {
        background: #8ce085;
        color: #000;
    }
    .info {
        background: lighten(#5aa6d1, 30%);
        color: #000;
    }
}
